import "./App.css";
import { Routes, Route, Navigate } from "react-router-dom";
import MicroReact from "./views/micro-react";

function App() {
  return (
    <div className="micro-matrix-app">
      <Routes>
        {/*  /micro-v-route */}
        <Route path="/" element={<Navigate replace to="/micro-v-route/" />} />
        <Route path="/base-r-route/cesium-lab" element={<MicroReact />} />
      </Routes>
      {/* vue3 */}
      <div id="micro-app"></div>
    </div>
  );
}

export default App;
